.context-menu {
    position: absolute;
    width: 200px;
    min-height: 100px;
    border: 1px solid #ccc;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    background-color: white;
    padding: 2px 0;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.13),
