                0 6px 6px rgba(0, 0, 0, 0.13);
    font-size: .8rem;
    color: black;
}

.context-option-group:not(:last-child) {
    padding: 5px 0 5px 0;
    border-bottom: 1px solid #ccc;
    border-bottom: 1px solid var(--border-color);
}

.context-option {
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
