:root {
    --backgroundColor: #ffffff;
    --textColor: #535353;
    --highlightColor: #d1d1d1;
    --textSize: 1rem;
    --viewportHeight: 5rem;
    --scrollbarColor: #b1b1b1;
    --scrollbarWidth: 5px;
}

.hidden {
    display: none;
}

.dropdown {
    width: 100%;
    max-height: 5rem;
    max-height: var(--viewportHeight);
    background-color: var(backgroundColor);
    border: 1px solid #535353;
    border: 1px solid var(--textColor);
    box-shadow: 0px 2px 2px #d1d1d1;
    box-shadow: 0px 2px 2px var(--highlightColor);
    overflow-y: auto;
}

.dropdown::-webkit-scrollbar {
    width: 5px;
    width: var(--scrollbarWidth);
    background-color: #d1d1d1;
    background-color: var(--highlightColor);
}
.dropdown::-webkit-scrollbar-thumb {
    width: 5px;
    width: var(--scrollbarWidth);
    height: 10px;
    background-color: #b1b1b1;
    background-color: var(--scrollbarColor);
}

.dropdown-choice {
    color: #535353;
    color: var(--textColor);
    background-color: #ffffff;
    background-color: var(--backgroundColor);
}

.dropdown-choice:hover {
    background-color: #d1d1d1;
    background-color: var(--highlightColor);
    transition: .1s;
}


.properties-menu {
  box-sizing: border-box;
  height: 100%;
  border: 2px solid rgba(0, 0, 0, .1);
  border-radius: 5px;
  background-color: white;
  box-shadow: -3px 10px 10px rgba(0, 0, 0, 0.1), -2px 5px 5px rgba(0, 0, 0, .1),
    -1px 2px 2px rgba(0, 0, 0, 0.1);
  padding: 5px;
}

.option {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  margin: 4px 0;
}

.color-picker {
  align-self: stretch;
  position: relative;
  width: 25px;
  height: 25px;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 0 5px;
}

.color-picker .popup {
  position: absolute;
  z-index: 100;
  top: 30px;
  right: 0;
  padding: 5px;
  background-color: white;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.1), 2px 3px 3px rgba(0, 0, 0, 0.1),
    1px 1px 1px rgba(0, 0, 0, 0.1);
}

.presets {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

.presets > div {
  border-radius: 3px;
  border: 1px solid gray;
  height: 21px;
  width: 21px;
  margin: 2px;
}

.presets > div:hover {
  outline: 1px solid black;
}

div.color-input {
  display: flex;
  flex-flow: row;
  align-items: stretch;
  margin-top: 5px;
  border-radius: 5px;
  border: 2px solid gray;
}

div.color-input:focus-within {
  box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.3);
}

.color-input div {
  overflow: hidden;
  background-color: lightgray;
  border-right: 2px solid gray;
  overflow: hidden;
  padding: 0 4px;
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
}

.color-input input {
  width: 100%;
  border: none;
  border-radius: inherit;
  outline: none;
  text-decoration: none;
}
.toolbar {
  display: flex;
  height: 25px;
  flex-flow: row;
  align-items: center;
  justify-content: flex-start;
  background-color: var(--app-lightblue);
}

.toolbar * {
  padding: 0 2px;
}

.toolbar span {
  padding: 0 2px;
  min-width: 6rem;
}
:root {
  --app-gray: rgba(66, 75, 84, 1);
  --app-green: rgba(112, 183, 126, 1);
  --app-lightblue: rgba(191, 215, 234, 1);
}

html {
  height: 100vh;
}

body {
  height: 100%;
  margin: 0;
}

#root {
  font-family: "Roboto", sans-serif;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Noto Sans JP", sans-serif;
}

.app {
  height: 100%;
  display: grid;
  grid-template-rows: 25px 1fr 25px;
  grid-template-columns: 50px 1fr 300px;
  grid-row-gap: 0px;
  row-gap: 0px;
  grid-column-gap: 0px;
  -webkit-column-gap: 0px;
          column-gap: 0px;
}

.header {
  grid-column: 1 / span 3;
  grid-row: 1;
}

.footer {
  grid-column: 1 / span 3;
  grid-row: 3;
}

.left-toolbar {
  grid-column: 1;
}

.right-toolbar {
  grid-column: 3;
  grid-row: 2;
  padding: 5px;
}

.content {
  border: 1px solid blue;
  position: relative;
  overflow-x: hidden;
}

